import type { NextApiRequest, NextApiResponse } from 'next';

import { NextRequest } from 'next/server';

export class PagesRouterAdapter {
  static convertRequest(req: NextApiRequest): NextRequest {
    const origin = req.headers.origin || 'http://localhost:3000';
    const url = new URL(req.url!, origin);

    // CrÃ©er les headers
    const headers = new Headers();
    Object.entries(req.headers).forEach(([key, value]) => {
      if (value) {
        headers.append(key, Array.isArray(value) ? value.join(', ') : value);
      }
    });

    // GÃ©rer le body en fonction du Content-Type
    let body: string | undefined;
    const contentType = headers.get('content-type') || '';
    
    if (contentType.includes('application/x-www-form-urlencoded')) {
      // Pour les requÃªtes form-data, utiliser URLSearchParams
      const formData = new URLSearchParams();
      if (req.body) {
        Object.entries(req.body).forEach(([key, value]) => {
          formData.append(key, String(value));
        });
      }
      body = formData.toString();
    } else if (req.body) {
      // Pour les autres types de requÃªtes, utiliser JSON
      body = JSON.stringify(req.body);
    }

    return new NextRequest(url.toString(), {
      method: req.method || 'GET',
      headers,
      body
    });
  }

  static async convertResponse(response: Response, res: NextApiResponse): Promise<void> {
    // Copier les headers
    response.headers.forEach((value, key) => {
      res.setHeader(key, value);
    });

    // DÃ©finir le status
    res.status(response.status);

    // Envoyer la rÃ©ponse
    const data = await response.json();
    res.json(data);
  }
}